@keyframes spin {
  from {
    transform: translateY(-50%)rotate(0);
  }

  to {
    transform: translateY(-50%)rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__details {
  position: absolute;
}

.stopwatch__details--minutes {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite spin;
}

.stopwatch__details--seconds {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite spin;
}

.stopwatch__details--center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch--speed-up .stopwatch__details--minutes {
  animation: 600s steps(60, end) infinite spin;
}

.stopwatch--speed-up .stopwatch__details--seconds {
  animation: 10s linear infinite spin;
}
